$root_color : #f3f3f3;
$main_color: #eaeaea;
$white_opacity: #ffffffda;
$red_message: #c02525;
$_black: #000000;
$_white: #ffffff;

$default_font: "Lobster Two", cursive;
$second_font: "Roboto", sans-serif;

$font_size_middle: 1rem;
$font_size_small: 0.8rem;
$font_size_large: 1.3rem;

$default_transition: 0.15s ease-in-out;

/* app */
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  user-select: none;
  font-family: $default_font;
}

html,
body,
#root {
  min-height: 100vh;
  background-color: $root_color;
  z-index: 1;
}

/* bar */
.bar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: $main_color;
  border-bottom: 0.1rem solid $_black;
  padding: 1rem 2rem;
  z-index: 3;
}

/* modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: $white_opacity;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
  transition: $default_transition;
}
.modal.active {
  z-index: 4;
  transition: $default_transition;
  opacity: 1;
}
.modal ._container {
  position: absolute;
  top: 20%;
  left: 50%;
  right: 0;
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1rem 2em;
  opacity: 0;
  background-color: $root_color;
  border: 0.1rem solid $_black;
  transform: translate(-50%, -40%);
  transition: $default_transition;
}
.modal.active ._container {
  opacity: 1;
  top: 40%;
  transition: $default_transition;
}


.modal_close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}

/* input */
._input {
  display: flex;
  justify-content: center;
  font-family: $second_font;
  margin-top: 0.5rem;
  padding: 0.2rem 1rem;
  min-height: 2.5rem;
  width: 70%;
  outline: none;
}
span._input {
  text-align: center;
}
span._input.red {
  text-align: center;
  color: $red_message;
}

