/*  min-height: 7rem;*/
/*}*/

/* button */
.button_section {
  position: absolute;
  bottom: -10%;
  right: 0.5rem;
  display: flex;
  justify-content: space-evenly;
  opacity: 0;
  transition: $default_transition;
}
._button {
  padding: 0.5rem 1rem;
  margin-left: 0.5rem;
  border: 0;
  background-color: $_white;
  font-size: $font_size_small;
  font-weight: 600;
  font-family: $second_font;
  cursor: pointer;
  transition: $default_transition;
  border-bottom: 0.09rem solid $_white;
  outline: none;
}

._button:hover {
  border-bottom: 0.09rem solid $_black;

}
._button:active {
  border-bottom: 0.09rem solid $_white;
}

/* link */
.link{
  display: block;
  margin: 0 0.5rem;
  border-bottom: 0.1rem solid transparent;
  transition: $default_transition;
}

.link:hover{
  border-bottom: 0.1rem solid $_black;
  transition: $default_transition;
}

.link a {
  display: block;
  font-family: $second_font;
  padding: 0.5rem 1rem;
  text-decoration: initial;
  color: $_black;
}

.link_container{
  display: flex;
  justify-content: center;
}


/* main */
.main {
  position: relative;
  display: flex;
  padding: 6rem 0 0 0;
}


/* title */
._title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: $font_size_large;
  font-weight: 800;
  overflow-wrap: break-word;
  text-align: center;
}
._title a {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: initial;
  color: $_black;
}


/* form */
._form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

._form_buttons {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* message */
.messages {
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
  z-index: 4;
  transition: $default_transition;
}

.message {
  position: relative;
  display: flex;
  justify-content: space-between;
  right: -15rem;
  width: 15rem;
  padding: 1rem;
  margin: 0.5rem 0;
  background-color: $main_color;
  border: 0.1rem solid $_black;
  opacity: 0;
  transition: $default_transition;
}

.message.active {
  right: 0.5rem;
  opacity: 1;
}

.message span {
  font-family: $second_font;
  color: #b61a1a;
  font-size: $font_size_middle;
  overflow-wrap: break-word;
  overflow: hidden;
}

.message_close {
  cursor: pointer;
}


/* category */
.categories {
  display: flex;
  flex-direction: column;
  width: 100%;
  z-index: 0;

  .category {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 0.5rem;

    .subCategories {
      height: 0;
      display: flex;
      flex-direction: column;
      opacity: 0;
      transition: $default_transition;
      z-index: -1;

      .subCategory {
        padding: 1rem;
        margin: 0 1rem;
        cursor: pointer;
        border-bottom: 0.1rem solid transparent;
        font-family: $second_font;
        transition: $default_transition;

        &:hover {
          border-bottom: 0.1rem solid $_black;
          transition: $default_transition;
        }
      }
      &:hover {
        opacity: 1;
      }
    }

    &.active {
      overflow: visible;

      & .subCategories {
        height: 10rem;
        overflow: scroll;
        opacity: 1;
        z-index: 1;
      }

      &.empty {
        overflow: visible;

        & .subCategories {
          height: auto;
          overflow: hidden;
          opacity: 1;
          z-index: 1;

          .subCategory {
            border-bottom: none;
            color: $red_message;
            cursor: default;
            &:hover {
              border-bottom: none;
            }
          }
        }
      }
      .category_text img {
        transform: rotate(180deg);
        transition: $default_transition;
      }

    }
    .category_text {
      display: flex;
      justify-content: space-between;
      background-color: $main_color;
      padding: 1rem;
      width: 100%;
      cursor: pointer;
      transition: $default_transition;

      img {
        transition: $default_transition;
      }
      &:hover {
        background-color: #cdcdcd;
      }
    }
    }
  }


/* loading */
.loading {
  position: fixed;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  background-color: $white_opacity;
  z-index: 2;
}

.loading_small {
  position: fixed;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: $white_opacity;
}

/* icon */
.icon {
  width: 3rem;
}

.icon.middle {
  width: 1.3rem;
  height: 1.3rem;
}

.icon.small {
  width: 0.8rem;
  height: 0.8rem;
}


/* not found 404 */
.not_found {
  padding: 6rem 1rem;
  font-family: $second_font;
}


/* non used */
.card {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-height: 13rem;
  width: 40rem;
  padding: 1rem 0.5rem 1rem 0.5rem;
  margin-top: 1rem;
  background-color: #f5f5f5;
  transition: $default_transition;
  overflow: hidden;
  opacity: 0.1;
}
.card:hover {
  box-shadow: 0 0 0.3rem 0.05rem #b3b3b3;
}
.card:hover .button_section {
  bottom: 4%;
  opacity: 1;
}
.card.deleted {
  visibility: hidden;
}
.card.animated {
  background-color: #e5e5e5;
  opacity: 1;
}

.list.large {
  font-size: $font_size_large;
}

.card_description {
  min-width: 30rem;
  margin-left: 1.5rem;
  font-size: $font_size_small;
  text-align: right;
  overflow-wrap: break-word;
  font-family: $second_font;
}